.PicLink-container img {
    width: 30vw;
    display: block;
    margin: 3vh 3vw;
}

.PicLink-container h2 {
    color: #535d63;
    text-align: center;
}

.wedding-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    width: 90vw;
}

.wedding-container h2 {
    margin-top: 3vh;
}

.weddingGrid {
    margin: 6vh 0;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: center;
            justify-content: center;

}

.snippet-container {
  position: relative;
  width: 25vw;
  display: -webkit-flex;
  display: flex;
  border: 1px solid #535d63;
  margin: 3vh;
}

.snippet-image {
  width: 100%;
  height: 100%;
  border-right: 1px solid #535d63;
}

.snippet-header {
  color: #535d63;
  margin-bottom: 1.5vh;
}

.onHoverTitle {
  color: #535d63;
}

.scale {
  -webkit-transition: -webkit-transform 0.1s ease-in;
  transition: -webkit-transform 0.1s ease-in;
  transition: transform 0.1s ease-in;
  transition: transform 0.1s ease-in, -webkit-transform 0.1s ease-in;
}

.scale:hover {
  -webkit-transform: scale(1.025);
          transform: scale(1.025);
}

.scalingImage-container h2 {
  margin-bottom: 2vh;
}

@media only screen and (min-width: 651px) {
  .scalingImage,
  .scale {
    width: 25vw;
  }
}

@media only screen and (max-width: 650px) {
  .onHover {
    opacity: 0.75;
    position: absolute;
    width: 50%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    background-color: white;
    height: 100%;
    height: auto;
    text-align: center;
    border: 1px solid #535d63;
  }
}

